//
//	Variables
//
$color-black: #000000;
$color-white: #ffffff;
$color-lego-yellow: #ffcf00;
$color-primary: #1f1f1f;
$color-secondary: #4f4f4f;
$color-tertiary: #9f9f9f;
/*
Add custom colors and other variables here
*/
$background-alpha: 0.7;


//
//	Mixins
//
@mixin background-alpha($color, $alpha) {
	background-color: rgba($color, $alpha) !important;
}


/*********************************************************************
	Colors
*********************************************************************/
.color-black-text {
	color: $color-black !important;
}
.color-black-background {
	background-color: $color-black !important;
}
.color-black-background-alpha {
	@include background-alpha($color-black, $background-alpha);
}

.color-white-text {
	color: $color-white !important;
}
.color-white-background {
	background-color: $color-white !important;
}
.color-white-background-alpha {
	@include background-alpha($color-white, $background-alpha);
}

.color-primary-text {
	color: $color-primary !important;
}
.color-primary-background {
	background-color: $color-primary !important;
}
.color-primary-background-alpha {
	@include background-alpha($color-primary, $background-alpha);
}

.color-secondary-text {
	color: $color-secondary !important;
}
.color-secondary-background {
	background-color: $color-secondary !important;
}
.color-secondary-background-alpha {
	@include background-alpha($color-secondary, $background-alpha);
}

.color-tertiary-text {
	color: $color-tertiary !important;
}
.color-tertiary-background {
	background-color: $color-tertiary !important;
}
.color-tertiary-background-alpha {
	@include background-alpha($color-tertiary, $background-alpha);
}

.no-background-image {
	background-image: none;
}

.form-background {
	@include background-alpha($color-tertiary, $background-alpha);
}

