//
//	Mixins
//

/*
Add font and custom mixins here
*/
@mixin font-base {
	font-family: 'Raleway', sans-serif;
}


/*********************************************************************
	Fonts
*********************************************************************/
/*
Add custom font classes here
*/
.font-light {
	@include font-base;
	font-weight: 300;
}
.font-regular {
	@include font-base;
	font-weight: 400;
}
.font-medium {
	@include font-base;
	font-weight: 500;
}
.font-bold {
	@include font-base;
	font-weight: 700;
}

.font-size-xsmall {
	font-size: .5em;
}
.font-size-small {
	font-size: .75em;
}
.font-size-base {
	font-size: 1em;
}
.font-size-medium {
	font-size: 1.25em;
}
.font-size-large {
	font-size: 1.5em;
}
.font-size-xlarge {
	font-size: 2em;
}

.line-height-small {
	line-height: 1em;
}